@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#page-wrapper {
  max-width: 66%;
  margin: 0 auto 0 auto;
  overflow: auto
}

#App {
  z-index: 2;
  position: absolute;  
  top: 0%;
  left: 0%;
  height: 100%;
  width: 100%;
}

.bg-image {
  background-image: url('https://pixabay.com/get/g2480f1afacd804a61a7b6db4a5babc48578d7da32b50df3d3c60b12f78c52282603903555693f14dd0a8a09a31927bbc7f1a49aaaee8ce366448f5713d022bcff20b84905f9cbc1fe635f04d577ef3d9_1920.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  opacity: 0.25;
  height: 100%;
  width: 100%;
  background-position: center;
  position: fixed;
  top:0;
}